import { IApi, IContact, IBootstrap, SignatureInfo } from "../IApi";

class Api implements IApi {
    bootstrap(): Promise<IBootstrap> {
        return new Promise((resolve) => {
            let response = {} as IBootstrap;

            const clientContacts: IContact[] = [{
                ContactId: "abc1",
                Email: "sehab@jake.com",
                FirstName: "Sehab",
                LastName: "Veljacic"
            } as IContact];

            const tmContacts: IContact[] = [{
                ContactId: "trer4",
                Email: "rajiv@rajiv.com",
                FirstName: "Rajiv",
                LastName: "Bradley",
                Phone: "9874568744"
            } as IContact];

            response.ClientContacts = clientContacts;
            response.TMContacts = tmContacts;
            response.EntityId = "asdfl333";
            response.EntityName = "Hustler Production";

            console.log(response);

            resolve(response);
        });
    }

    sendTmsa(signatureInfo: SignatureInfo): Promise<boolean> {
        return new Promise((resolve) => {
            console.log("signature info", signatureInfo);
            resolve(true);
        });
    }
}

export default Api;


