export interface IKeyValuePair<TKey, TValue> {
    key: TKey,
    value: TValue;
}

var handler = {
    get: function <TKey>(target: any, property: TKey, receiver: any) {
        if (property as any in target) {
            return target[property];
        }

        return target.get(property);
    }
}

class KeyValuePair<TKey, TValue> {
    private _pairs: IKeyValuePair<TKey, TValue>[] = [];

    [key: string]: any;

    constructor() {
        return new Proxy(this, handler);
    }

    add(key: TKey, value: TValue) {
        const index = this._pairs.length;
        this.insert(index, key, value);
    }

    containsKey(key: TKey): boolean {
        return this._pairs.findIndex(x => x.key === key) > -1;
    }

    insert(index: number, key: TKey, value: TValue) {
        if (this._pairs.findIndex(x => x.key === key) > -1)
            throw Error("Key must be unique.");

        this._pairs.splice(index, 0, { key, value } as IKeyValuePair<TKey, TValue>);
    }

    sort(compareFn?: ((a: IKeyValuePair<TKey, TValue>, b: IKeyValuePair<TKey, TValue>) => number) | undefined): IKeyValuePair<TKey, TValue>[] {
        return this._pairs.sort(compareFn) as IKeyValuePair<TKey, TValue>[];
    }

    get keys(): TKey[] {
        return this._pairs.map(x => x.key);
    }

    get values(): TValue[] {
        return this._pairs.map(x => x.value);
    }

    get(key: TKey): TValue {
        return this._pairs.find(x => x.key === key)!.value;
    }

    getValueByIndex(index: number): TValue {
        return this._pairs[index].value;
    }

    getKeyValues(): IKeyValuePair<TKey, TValue>[] {
        return this._pairs;
    }
}


export { KeyValuePair };


