import { ModuleOf } from "./ModuleOf";
import { IApi, IBootstrap, SignatureInfo } from "./IApi";
declare var api: ModuleOf<IApi>;

class ApiController implements IApi {
    bootstrap(): Promise<IBootstrap> {
        return api.default.prototype.bootstrap();
    }

    sendTmsa(signatureInfo: SignatureInfo): Promise<boolean> {
        return api.default.prototype.sendTmsa(signatureInfo);
    }
}

const apiController = new ApiController();

export default apiController;


