import * as React from 'react';
import Header from './Header';
import store from "../Store/Store";
import {
    CssBaseline, Theme, WithStyles, Table, TableHead, TableRow,
    TableCell, TableBody, Button, Link,
    CircularProgress, Snackbar, SnackbarContent, IconButton, Icon
} from '@material-ui/core';
import { withStyles, createStyles } from '@material-ui/styles';
import { green, red } from "@material-ui/core/colors";
import { observer } from 'mobx-react';
import TmsaModal from "./TmsaModal";

const styles = (theme: Theme) => createStyles({
    bodyRoot: {
        padding: 6
    },
    snackBarSuccess: {
        backgroundColor: green["600"],
        color: "black"
    },
    snackBarDanger: {
        backgroundColor: red["600"],
        color: "white"
    }
});

interface IProps extends WithStyles<typeof styles> {

}

interface IState {
    open: boolean;
    snackBarOpen: boolean;
    snackBarMessage: string;
    snackBarSuccess?: boolean;
}

@observer
class App extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            open: false,
            snackBarMessage: "",
            snackBarOpen: false
        }
    }

    componentDidMount() {
        store.loadBootstrapAsync();
    }


    getTmsaLink() {
        if (!store.state.HasExistingTmsa)
            return "None";

        return <Link target="_blank" href={store.state.ExistingTmsaLink}>View Signed TMSA</Link>
    }

    getTmsaDate() {
        if (!store.state.HasExistingTmsa || store.state.ExistingTmsaDate === undefined)
            return "None";

        return store.state.ExistingTmsaDate.toDateString();
    }

    getTmsaCurrentStage() {
        if (store.state.Status !== undefined)
            return store.state.Status;

        return "Current";
    }

    onOpenModal = () => {
        this.setState({ open: true });
    }

    onCloseModal = () => {
        this.setState({ open: false });
        store.clearModal();
    }

    onSignatureSent = (success: boolean) => {
        this.setState({
            snackBarSuccess: success,
            snackBarMessage: success ? "Signature has been sent successfully." : "There was an issue sending the TMSA for signature.",
            snackBarOpen: true,
            open: false
        })
    }

    render() {
        const { classes } = this.props;

        if (store.isLoading)
            return <CircularProgress size={40} />

        return (
            <>
                <CssBaseline />
                <Header />
                <div className={classes.bodyRoot}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>
                                    TMSA
                                </TableCell>
                                <TableCell>
                                    Upload Date
                                </TableCell>
                                <TableCell>
                                    TMSA Status
                                    </TableCell>
                                <TableCell>
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            <TableRow>
                                <TableCell>
                                    {this.getTmsaLink()}
                                </TableCell>
                                <TableCell>
                                    {this.getTmsaDate()}
                                </TableCell>
                                <TableCell>
                                    {this.getTmsaCurrentStage()}
                                </TableCell>
                                <TableCell>
                                    <Button onClick={this.onOpenModal}>{store.state.HasExistingTmsa ? "Replace" : "Add New TMSA"}</Button>
                                </TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </div>
                <TmsaModal isOpen={this.state.open} onClose={this.onCloseModal} onCompleted={this.onSignatureSent} />

                <Snackbar open={this.state.snackBarOpen} anchorOrigin={{ vertical: "bottom", horizontal: "right" }}>
                    <SnackbarContent className={this.state.snackBarSuccess ? classes.snackBarSuccess : classes.snackBarDanger} action={[
                        <IconButton key="close" aria-label="close" color="inherit" onClick={() => this.setState({ snackBarOpen: false })}>
                            <Icon>close</Icon>
                        </IconButton>
                    ]} message={this.state.snackBarMessage}>
                    </SnackbarContent>
                </Snackbar>
            </>
        );
    }
}




export default withStyles(styles)(App);


