import * as React from 'react';
import {
    Modal, Card, CardContent, Typography, CardActions, Button, createStyles,
    WithStyles, Theme, Divider, CardHeader, Table, TableHead, TableRow,
    TableCell, TableBody, Input, MenuItem, Select, FormControl, InputLabel,
    CircularProgress
} from '@material-ui/core';
import store from "../Store/Store";
import { withStyles } from '@material-ui/styles';
import { IKeyValuePair } from '../Helpers/KeyValuePair';
import { observer } from 'mobx-react';
import { IModalState } from 'ClientApp/api/IApi';

const styles = (theme: Theme) => createStyles({
    modalStyle: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    },
    cardMax: {
        maxWidth: "80%"
    },
    loadingStyle: {
        display: "flex",
        justifyContent: "center",
        padding: 20,
        flexFlow: "column",
        alignItems: "center"
    }
});

interface IProps extends WithStyles<typeof styles> {
    onClose: () => void;
    isOpen: boolean;
    onCompleted: (success: boolean) => void;
}

interface IState {
    loading: boolean;
    isError: boolean;
    errorMessage: string[];
}

@observer
class TmsaModal extends React.Component<IProps, IState> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            loading: false,
            isError: false,
            errorMessage: []
        }

    }

    getClientSigners() {
        const contacts = store.getClientContacts;
        if (contacts === undefined)
            return null;

        return contacts.keys.map((x: string, i: number) => {
            return <MenuItem key={i} value={x}>{contacts[x]}</MenuItem>
        })
    }

    getTmOfficerSigners() {
        const contacts = store.getTmContacts;
        if (contacts === undefined)
            return null;

        return contacts.keys.map((x: string, i: number) => {
            return <MenuItem key={i} value={x}>{contacts[x]}</MenuItem>
        });
    }


    onClientSignerChange = (e: React.ChangeEvent<{ name?: any, value: any }>) => {
        this.setState({ isError: false });
        store.setClientSigner(e.target.value);
    }

    onClientSignerCodeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({ isError: false });
        store.setClientSignerCode(e.target.value);
    }

    onTmSignerChange = (e: React.ChangeEvent<{ name?: any, value: any }>) => {
        this.setState({ isError: false });
        store.setTmoSigner(e.target.value);
    }

    onTmSignerCodeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({ isError: false });
        store.setTmoSignerCode(e.target.value);
    }
    onClose = () => {
        this.setState({ isError: false, loading: false });
        this.props.onClose();
    }

    onSendTmsa = async () => {
        const errors: string[] = [];

        if (store.modalState.bankSignerCode.length < 4 || store.modalState.bankSignerCode.length > 6)
            errors.push("Client signer code must be between 4 and 6 characters long.");

        if (store.modalState.bankSignerId === undefined || store.modalState.bankSignerId.length === 0)
            errors.push("Client signer is missing.")

        if (store.modalState.tmoSignerCode.length < 4 || store.modalState.tmoSignerCode.length > 6)
            errors.push("TM officer signer code must be between 4 and 6 characters long.");

        if (store.modalState.tmoSignerId === undefined || store.modalState.tmoSignerId.length === 0)
            errors.push("TM officer signer is missing.")


        if (errors.length > 0) {
            this.setState({ isError: true, errorMessage: errors, loading: false })
            return;
        }

        this.setState({ loading: true }, async () => {
            const state = await store.sendTmsaAsync();
            this.setState({ loading: false, isError: false });
            store.modalState = {} as IModalState;
            this.props.onCompleted(state);
        })
    }

    generateLoading = () => {
        return <div className={this.props.classes.loadingStyle}>
            <Typography variant="h5">Sending for Signature...</Typography>
            <CircularProgress size={40} />
        </div>
    }

    generateErrorMessage = () => {
        if (!this.state.isError)
            return null;

        return this.state.errorMessage.map((x: string, i: number) => {
            return <Typography variant="body2" color="error">
                {x}
            </Typography>
        });
    }

    generateContent = () => {
        return (<Table>
            <TableBody>
                <TableRow>
                    <TableCell>
                        <FormControl>
                            <InputLabel htmlFor="client-signer">Client Signer</InputLabel>
                            <Select value={store.modalState.bankSignerId || ""} style={{ minWidth: 150 }}
                                inputProps={{ id: "client-signer" }} onChange={this.onClientSignerChange}>
                                {this.getClientSigners()}
                            </Select>
                        </FormControl>
                    </TableCell>
                    <TableCell>
                        <FormControl>
                            <InputLabel htmlFor="banker-code">Security Code</InputLabel>
                            <Input value={store.modalState.bankSignerCode || ""} inputProps={{ id: "banker-code" }}
                                onChange={this.onClientSignerCodeChange} />
                        </FormControl>
                    </TableCell>
                </TableRow>
                <TableRow>
                    <TableCell>
                        <FormControl>
                            <InputLabel htmlFor="tm-signer">Tm Officer Signer</InputLabel>
                            <Select value={store.modalState.tmoSignerId || ""} style={{ minWidth: 150 }}
                                inputProps={{ id: "bank-signer" }} onChange={this.onTmSignerChange}>
                                {this.getTmOfficerSigners()}
                            </Select>
                        </FormControl>
                    </TableCell>
                    <TableCell>
                        <FormControl>
                            <InputLabel htmlFor="tm-code">Security Code</InputLabel>
                            <Input inputProps={{ id: "tm-code" }} value={store.modalState.tmoSignerCode || ""}
                                onChange={this.onTmSignerCodeChange}
                            />
                        </FormControl>
                    </TableCell>
                </TableRow>
            </TableBody>
        </Table>);
    }



    render() {
        const { classes } = this.props;

        return (<Modal open={this.props.isOpen} className={classes.modalStyle}>
            <Card className={classes.cardMax}>
                <CardHeader title={store.state.HasExistingTmsa ? "Replace TMSA" : "Add New TMSA"} />
                <CardContent>
                    {this.generateErrorMessage()}
                    {this.state.loading ? this.generateLoading() : this.generateContent()}
                </CardContent>
                <CardActions style={{ display: "flex", justifyContent: "space-around" }}>
                    <Button color="secondary" onClick={this.onClose}>Close</Button>
                    <Button color="primary" onClick={this.onSendTmsa}>Submit</Button>
                </CardActions>
            </Card>
        </Modal>)
    }
}

export default withStyles(styles)(TmsaModal);


