import * as React from 'react';
import { AppBar, Typography, Theme, createStyles, IconButton, Icon, Toolbar } from '@material-ui/core';
import { WithStyles, withStyles } from '@material-ui/styles';
import { observer } from "mobx-react";
import store from "../Store/Store";
import { fontWeight } from '@material-ui/system';

const styles = (themes: Theme) =>
    createStyles({
        marginBottom: {
            marginBottom: 6
        },
        toolBar: {
            backgroundColor: "white",
            color: "#000"
        },
        headerTop: {
            lineHeight: 1,
            color: "#4a4a56",
            fontSize: "0.80em",
            fontWeight: 500,
            marginLeft: "1.1px"
        },
        headerBottom: {
            lineHeight: 1,
            color: "#000"
        }
    });

interface IProps extends WithStyles<typeof styles> {
}

@observer
class Header extends React.Component<IProps, any>{

    render() {
        const { classes } = this.props;
        return (
            <>
                <AppBar position="relative" className={classes.marginBottom} elevation={0}>
                    <Toolbar className={classes.toolBar}>
                        <IconButton color="inherit" href={`/${store.state.EntityId}`}>
                            <Icon color="inherit">keyboard_arrow_left</Icon>
                        </IconButton>
                        <div>
                            <Typography variant="subtitle2" className={classes.headerTop}>Entity - TMSA</Typography>
                            <Typography variant="h5" className={classes.headerBottom}>{store.state.EntityName}</Typography>
                        </div>
                    </Toolbar>
                </AppBar>
            </>
        );
    }
}

export default withStyles(styles)(Header);


