import { observable, action, computed } from "mobx";
import apiController from "../api/ApiController";
import { ISiteState, IModalState, IContact, SignatureInfo, TmoUser, SignerInfo } from "../api/IApi";
import { KeyValuePair } from "../Helpers/KeyValuePair";


class Store {
    @observable state: ISiteState = {} as ISiteState;
    @observable modalState: IModalState = { bankSignerCode: "", tmoSignerCode: "" } as IModalState;
    @observable isLoading: boolean = true;

    @action async loadBootstrapAsync() {
        const result = await apiController.bootstrap();

        this.state.EntityName = result.EntityName;
        this.state.EntityId = result.EntityId;
        this.state.HasExistingTmsa = false;

        if (result.ExistingTMSALink !== undefined && result.ExistingTMSALink.length > 0) {
            this.state.HasExistingTmsa = true;
            this.state.ExistingTmsaLink = result.ExistingTMSALink;
            if (result.ExistingTMSADate !== undefined)
                this.state.ExistingTmsaDate = new Date(result.ExistingTMSADate);
        }

        this.state.ClientContacts = result.ClientContacts;
        this.state.Status = result.Status;
        this.state.TmContacts = result.TMContacts;
        this.isLoading = false;
    }

    @action async sendTmsaAsync(): Promise<boolean> {
        const tmoSigners = this.state.TmContacts;
        const clientSigners = this.state.ClientContacts;

        const signers: SignerInfo[] = [];
        signers.push(this.getSignerInfo(tmoSigners, this.modalState.tmoSignerId, 2, "TMS", this.modalState.tmoSignerCode));
        signers.push(this.getSignerInfo(clientSigners, this.modalState.bankSignerId, 1, "PS", this.modalState.bankSignerCode));

        const signature = {
            Signer: signers
        } as SignatureInfo;

        const results = await apiController.sendTmsa(signature);

        return results;
    }

    getSignerInfo(contacts: IContact[], id: string, priority: number, signerCode: string, securityCode: string): SignerInfo {
        const contact = contacts.find(x => x.ContactId === id);
        if (contact == null || contact === undefined)
            return {} as SignerInfo;

        return {
            Id: id,
            Priority: priority,
            SignerCode: signerCode,
            SecurityCode: securityCode,
            Tmo: {
                Email: contact.Email,
                FirstName: contact.FirstName,
                LastName: contact.LastName,
                Phone: contact.Phone
            }
        } as SignerInfo;
    }

    @computed get getClientContacts(): KeyValuePair<string, string> | undefined {
        const keyValue = new KeyValuePair<string, string>();
        if (this.state.ClientContacts === undefined)
            return undefined;

        this.state.ClientContacts.forEach(x => {
            keyValue.add(x.ContactId, `${x.FirstName} ${x.LastName} (${x.Email})`);
        });

        return keyValue;
    }

    @computed get getTmContacts(): KeyValuePair<string, string> | undefined {
        const keyValue = new KeyValuePair<string, string>();
        if (this.state.TmContacts === undefined)
            return undefined;

        this.state.TmContacts.forEach(x => {
            keyValue.add(x.ContactId, `${x.FirstName} ${x.LastName} (${x.Email})`);
        });

        return keyValue;
    }

    @action getSecurityCode(contact?: IContact): string {
        if (contact === null || contact === undefined)
            return "";

        if (contact.Phone === null || contact.Phone === undefined)
            return "0000";

        return contact.Phone.substr(contact.Phone.length - 4);
    }

    @action setClientSigner(id: string) {
        this.modalState.bankSignerId = id;
        const contact = store.state.ClientContacts.find(x => x.ContactId === id);
        this.modalState.bankSignerCode = this.getSecurityCode(contact);
    }

    @action setClientSignerCode(code: string) {
        this.modalState.bankSignerCode = this.getCode(code);
    }

    @action setTmoSigner(id: string) {
        this.modalState.tmoSignerId = id;
        const contact = store.state.TmContacts.find(x => x.ContactId === id);
        this.modalState.tmoSignerCode = this.getSecurityCode(contact);
    }

    @action setTmoSignerCode(code: string) {
        this.modalState.tmoSignerCode = this.getCode(code);
    }

    @action clearModal() {
        this.modalState = { bankSignerCode: "", tmoSignerCode: "" } as IModalState;
    }

    private isNumeric(code: string): boolean {
        if (!Number(code))
            return false;

        return true;
    }

    private getCode(code: string) {
        const replacedCode = code.replace(/[^\d]/g, "");

        if (replacedCode.length > 6)
            return replacedCode.substr(0, 6);

        return replacedCode;
    }
}

const store = new Store();

export default store;


